<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.title" @input="change" placeholder="标题"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd">新增</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="users" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<!-- <el-table-column type="index" width="60">
			</el-table-column> -->
			<el-table-column prop="id" label="ID" width="80" sortable>
			</el-table-column>
			<el-table-column label="标题" prop="title" style="width: 33.3%;" >
			</el-table-column>
			<el-table-column label="发布时间" prop="time" style="width: 33.3%;" >
			</el-table-column>
			<el-table-column prop="content" label="发布内容"   style="width: 33.3%;" >
			</el-table-column>
		
			
			<el-table-column label="操作" width="150">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!--编辑界面-->
		<el-dialog title="编辑" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
			
			<el-form-item label="标题" prop="title">
				<el-input v-model="editForm.title" auto-complete="off"></el-input>
			</el-form-item>
			<el-form-item label="发布时间" prop="time" >
				<el-input v-model="editForm.time" auto-complete="off"></el-input>
			</el-form-item>
		<el-form-item prop="content" label="发布内容"  >
			<el-input v-model="editForm.content" auto-complete="off"></el-input>
		</el-form-item>
			
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>

		<!--新增界面-->
		<el-dialog title="新增" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
		
			<el-form-item  label="标题" prop="title">
				<el-input v-model="addForm.title" auto-complete="off"></el-input>
			</el-form-item>
				<el-form-item label="发布时间" prop="time">
					<el-input v-model="addForm.time" auto-complete="off"></el-input>
				</el-form-item>
			<el-form-item prop="content" label="发布内容">
				<el-input v-model="addForm.content" auto-complete="off"></el-input>
			</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import {getarticlesList,removearticles,addarticles,editarticles} from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					title: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//列表选中列

				editFormVisible: false,//编辑界面是否显示
				editLoading: false,
				editFormRules: {
					title: [
						{ required: true, message: '请输入标题', trigger: 'blur' }
					]
				},
				//编辑界面数据
				editForm: {
				
				},

				addFormVisible: false,//新增界面是否显示
				addLoading: false,
				addFormRules: {
				title: [
					{ required: true, message: '请输入标题', trigger: 'blur' }
				]
				},
				//新增界面数据
				addForm: {
				
				}

			}
		},
		methods: {
			//姓名输入框值发生改变
			
			change(){
				
				if(this.filters.title==""){
					this.getUsers()
				}
			},
		
		
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//获取用户列表
			getUsers() {
				let para = {
				currentPage:this.page,
					title: this.filters.title,
					pagesize:20
				};
				// this.listLoading = true;
				//NProgress.start();
		//console.log(this.filters.name)
				getarticlesList(para).then(data => {
					//console.log(data)
					this.users=data.list
					this.total=data.total
						//this.listLoading = false;
						//NProgress.done();
					//NProgress.done();
				});
			},
			//删除
			handleDel: function (index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					//this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					console.log(para)
					removearticles(para).then((res) => {
						console.log(res)
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.initWebSocket();
						this.getUsers();
					});
					//console.log(JSON.parse(sessionStorage.getItem('user')).username )
				}).catch(() => {

				});
			},
			//显示编辑界面
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//显示新增界面
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					// name: '',
					// sex: -1,
					// age: 0,
					// birth: '',
					// addr: ''
				};
			},
			//编辑
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							console.log(para)
							editarticles(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								console.log(res)
								if(res.code==200){
									this.$message({
										message: '提交成功',
										type: 'success'
									});
									this.initWebSocket();
									this.$refs['editForm'].resetFields();
									this.editFormVisible = false;
									this.getUsers();
								}else{
									this.$message({
										message: res.message,
										type: 'warning'
									});
								}
							
							});
						});
					}
				});
			},
			//新增
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							console.log(para)
							// para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							addarticles(para).then((res) => {
								console.log(res)
								this.addLoading = false;
									if(res.code==200){
										
										//NProgress.done();
										this.$message({
											message: '提交成功',
											type: 'success'
										});
										 this.initWebSocket();
										this.$refs['addForm'].resetFields();
										this.addFormVisible = false;
									
										}else{
											this.$message({
												message: res.message,
												type: 'warning'
											});
											this.$refs['addForm'].resetFields();
											this.addFormVisible = false;
											
										}
							this.getUsers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					//this.listLoading = true;
					//NProgress.start();
					let para = { idd: ids };
				
				var k=para.idd
				k=k.split(',')//将id列表转换数组
					
					var flag=false
				
					for(var i=0;i<k.length;i++){
						
						removearticles({id:k[i]}).then((res) => {
							//console.log(res)
							if(res.code!=200){
								flag=true
							}
					
						
						});
					}
					if(flag!=true){
						let that=this
						
						setTimeout (function(){
							that.listLoading = false;
							that.$message({
								message: '删除成功',
								type: 'success'
							});
							
								that.getUsers();
						},200)
					}
					this.initWebSocket();
			
				}).catch(() => {

				});
			},
			   initWebSocket(){ //初始化weosocket
			        const wsuri = "ws://biyeji.uicp.net:8001";
			        this.websock = new WebSocket(wsuri);
			        this.websock.onmessage = this.websocketonmessage;
			        this.websock.onopen = this.websocketonopen;
			        this.websock.onerror = this.websocketonerror;
			        this.websock.onclose = this.websocketclose;
			      },
				   websocketonopen(){ //连接建立之后执行send方法发送数据
				     let that=this
				          let actions = "该更新了"//发送该更新了提示app那边更新消息
				          that.websocketsend(actions);
						  console.log("我发了")
							setTimeout (function(){
							that.websock.close()//发送完关闭连接
							},200)
				        },
				        websocketonerror(){//连接建立失败重连
				          this.initWebSocket();
				        },
				        websocketonmessage(e){ //数据接收
				    //       const redata = JSON.parse(e.data);
						  // console.log(e)
				        },
				        websocketsend(Data){//数据发送
				          this.websock.send(Data);
				        },
				        websocketclose(e){  //关闭
				          console.log('断开连接',e);
				        },
		},
		created(){
			
		},
		    destroyed() {
		      this.websock.close() //离开路由之后断开websocket连接
		    },
		mounted() {
			 this.getUsers();
			
	
		}
	}

</script>

<style scoped>

</style>


