import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
// import Table from './views/nav1/Table.vue'
// import Form from './views/nav1/Form.vue'
// import user from './views/nav1/user.vue'
import admin from './views/admin/admin.vue'
// import Page5 from './views/nav2/Page5.vue'
import teacher_user from './views/teacher/teacher_user.vue'
import teacher_phone from './views/teacher/teacher_phone.vue'
 import echarts from './views/charts/echarts.vue'
 import suggest from './views/system/suggest.vue'
 import classes from './views/system/classes.vue'
  import news from './views/system/news.vue'
    import student_user from './views/student/student_user.vue'
	  import articles from './views/system/articles.vue'
	    import records from './views/student/records.vue'
		  import checktimes from './views/student/checktimes.vue'
let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '首页',
        iconCls: 'el-icon-s-home',//图标样式class
    
		children: [
		    { path: '/echarts', component: echarts, name: '用户数量统计图' }
		]
    },
    {
        path: '/',
        component: Home,
        name: '管理员',
        iconCls: 'el-icon-user-solid',
        children: [
            { path: '/admin', component: admin, name: '管理员管理' },
           
        ]
    },
	{
	    path: '/',
	    component: Home,
	    name: '教师',
	    iconCls: 'el-icon-s-custom',
	    children: [
	        { path: '/teacher_user', component: teacher_user, name: '教师信息管理' },
	        { path: '/teacher_phone', component: teacher_phone, name: '教师电话管理' },
	    ]
	},
	{
	    path: '/',
	    component: Home,
	    name: '学生',
	    iconCls: 'el-icon-s-custom',
	    children: [
	        { path: '/student_user', component: student_user, name: '学生信息管理' },
	        // { path: '/articles', component: articles, name: '学生答辩管理' },
			  { path: '/records', component: records, name: '学生周记管理' },
			    { path: '/checktimes', component: checktimes, name: '学生签到管理' },
	    ]
	},
	{
	    path: '/',
	    component: Home,
	    name: '系统',
	    iconCls: 'el-icon-s-tools',
	    children: [
	        { path: '/news', component: news, name: '新闻信息管理' },
	        { path: '/classes', component: classes, name: '班级管理' },
			  { path: '/suggest', component: suggest, name: '用户建议管理' },
		 { path: '/articles', component: articles, name: '系统推送消息' },
	    ]
	},
    // {
    //     path: '/',
    //     component: Home,
    //     name: '',
    //     iconCls: 'fa fa-address-card',
    //     leaf: true,//只有一个节点
    //     children: [
    //         { path: '/page6', component: Page6, name: '导航三' }
    //     ]
    // },
  //   {
  //       path: '/',
  //       component: Home,
  //       name: 'Charts',
  //       iconCls: 'fa fa-bar-chart',
		// children: [
		//     { path: '/main', component: Main, name: '主页', hidden: true },
		//     { path: '/table', component: Table, name: 'Table' },
		//     { path: '/form', component: Form, name: 'Form' },
		//     { path: '/user', component: user, name: '列表' },
		// ],
    
  //   },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


