<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.Type" @input="change" placeholder="类型"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd">新增</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="users" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<!-- <el-table-column type="index" width="60">
			</el-table-column> -->
			<el-table-column prop="id" label="ID" width="80" sortable>
			</el-table-column>
			<el-table-column label="类型" prop="Type" width="200" >
			</el-table-column>
			<el-table-column prop="Title" label="新闻标题"  :show-overflow-tooltip="true"  width="200" >
			</el-table-column>
		<el-table-column prop="Text" label="新闻内容" :show-overflow-tooltip="true"    width="200" >
		</el-table-column>
			<el-table-column prop="ImgSrc" label="新闻大图" :show-overflow-tooltip="true"  width="200">
			</el-table-column>
			<el-table-column prop="Time" label="发表时间"  width="200" >
			</el-table-column>
			<el-table-column prop="Browse" label="浏览量"  width="200" >
			</el-table-column>
			<el-table-column label="操作" width="150">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!--编辑界面-->
		<el-dialog title="编辑" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
			
			<el-form-item label="类型" prop="Type">
				<!-- <el-input v-model="editForm.Type" auto-complete="off"></el-input> -->
				<el-select v-model="editForm.Type" auto-complete="off" style="width: 100%;" placeholder="选择类型">
				  <el-option v-for="item in newsType"
				             :key="item.value"
				             :label="item.label"
				             :value="item.value"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item prop="Title" label="新闻标题">
				<el-input v-model="editForm.Title" auto-complete="off"></el-input>
		
			</el-form-item>
			
			          
			           
		<el-form-item prop="Text" label="新闻内容">
			<el-input v-model="editForm.Text" auto-complete="off"></el-input>
		</el-form-item>
		<el-form-item prop="ImgSrc" label="新闻大图">
			<el-input v-model="editForm.ImgSrc" auto-complete="off"></el-input>
		</el-form-item>
		<el-form-item prop="Time" label="发表时间">
			<el-input v-model="editForm.Time" auto-complete="off"></el-input>
		</el-form-item>
		<el-form-item prop="Browse" label="浏览量">
			<el-input v-model="editForm.Browse" auto-complete="off"></el-input>
		</el-form-item>
		
			
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>

		<!--新增界面-->
		<el-dialog title="新增" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
		
			
				<el-form-item label="类型" prop="Type">
				<!-- 	<el-input v-model="addForm.Type" auto-complete="off"></el-input> -->
				<el-select v-model="addForm.Type" auto-complete="off" placeholder="选择类型">
				  <el-option style="width: 100%;" v-for="item in newsType"
				             :key="item.value"
				             :label="item.label"
				             :value="item.value"></el-option>
				</el-select>
				</el-form-item>
				<el-form-item prop="Title" label="新闻标题">
					<el-input v-model="addForm.Title" auto-complete="off"></el-input>
			
				</el-form-item>
			<el-form-item prop="Text" label="新闻内容">
				<el-input v-model="addForm.Text" auto-complete="off"></el-input>
			</el-form-item>
			<el-form-item prop="ImgSrc" label="新闻大图">
				<el-input v-model="addForm.ImgSrc" auto-complete="off"></el-input>
			</el-form-item>
			<el-form-item prop="Time" label="发表时间">
				<el-input v-model="addForm.Time" auto-complete="off"></el-input>
			</el-form-item>
			<el-form-item prop="Browse" label="浏览量">
				<el-input v-model="addForm.Browse" auto-complete="off"></el-input>
			</el-form-item>
			
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getnewsList,removenews,addnews,editnews} from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					Type: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//列表选中列

				editFormVisible: false,//编辑界面是否显示
				editLoading: false,
				editFormRules: {
					Type: [
						{ required: true, message: '请输入类型', trigger: 'blur' }
					]
				},
				//编辑界面数据
				editForm: {
				
				},
                 newsType:[
					 {
						 label:'学院要闻',
						 value:'学院要闻'
					 },
					 {
					 						 label:'校园动态',
					 						 value:'校园动态'
					 },
					 {
					 						 label:'媒体聚焦',
					 						 value:'媒体聚焦'
					 },
					 {
					 						 label:'学术讲座',
					 						 value:'学术讲座'
					 }
				 ],
				addFormVisible: false,//新增界面是否显示
				addLoading: false,
				addFormRules: {
				Type: [
					{ required: true, message: '请输入类型', trigger: 'blur' }
				]
				},
				//新增界面数据
				addForm: {
				
				}

			}
		},
		methods: {
			//姓名输入框值发生改变
			
			change(){
			
				if(this.filters.Type==""){
					this.getUsers()
				}
			},
		
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//获取用户列表
			getUsers() {
				let para = {
				currentPage:this.page,
					Type: this.filters.Type,
					pagesize:20
				};
				// this.listLoading = true;
				//NProgress.start();
		//console.log(this.filters.name)
				getnewsList(para).then(data => {
					//console.log(data)
					this.users=data.list
					this.total=data.total
						//this.listLoading = false;
						//NProgress.done();
					//NProgress.done();
				});
			},
			//删除
			handleDel: function (index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					//this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					console.log(para)
					removenews(para).then((res) => {
						console.log(res)
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getUsers();
					});
					//console.log(JSON.parse(sessionStorage.getItem('user')).username )
				}).catch(() => {

				});
			},
			//显示编辑界面
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//显示新增界面
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					// name: '',
					// sex: -1,
					// age: 0,
					// birth: '',
					// addr: ''
				};
			},
			//编辑
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							console.log(para)
							editnews(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								console.log(res)
								if(res.code==200){
									this.$message({
										message: '提交成功',
										type: 'success'
									});
									this.$refs['editForm'].resetFields();
									this.editFormVisible = false;
									this.getUsers();
								}else{
									this.$message({
										message: res.message,
										type: 'warning'
									});
								}
							
							});
						});
					}
				});
			},
			//新增
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							console.log(para)
							// para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							addnews(para).then((res) => {
								console.log(res)
								this.addLoading = false;
									if(res.code==200){
										
										//NProgress.done();
										this.$message({
											message: '提交成功',
											type: 'success'
										});
										this.$refs['addForm'].resetFields();
										this.addFormVisible = false;
										
										}else{
											this.$message({
												message: res.message,
												type: 'warning'
											});
											this.$refs['addForm'].resetFields();
											this.addFormVisible = false;
											
										}
							this.getUsers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					//this.listLoading = true;
					//NProgress.start();
					let para = { idd: ids };
				
				var k=para.idd
				k=k.split(',')//将id列表转换数组
					
					var flag=false
				
					for(var i=0;i<k.length;i++){
						
						removenews({id:k[i]}).then((res) => {
							//console.log(res)
							if(res.code!=200){
								flag=true
							}
					
						
						});
					}
					if(flag!=true){
						let that=this
						setTimeout (function(){
							that.listLoading = false;
							that.$message({
								message: '删除成功',
								type: 'success'
							});
								that.getUsers();
						},200)
					}
			
				}).catch(() => {

				});
			}
		},
		mounted() {
			 this.getUsers();
			
	
		}
	}

</script>

<style scoped>

</style>


