<template>
	<section class="chart-container">

		<el-row style="width: 100%;">
			<el-col :span="12">
				<div id="chartColumn" style="width:100%; height:500px;"></div>
			</el-col>
			<el-col :span="12">
				<div id="chartColumn2" style="width:100%; height:500px;"></div>
			</el-col>

		
		</el-row>
	</section>
</template>

<script>
	import echarts from 'echarts'
	import {
		selectStudent_echart
	} from '../../api/api.js';
	import {
		selectTeacher_echart
	} from '../../api/api.js';
	
	export default {
		data() {
			return {
				timelist: [],
				datalist: [],
				timelist2: [],
				datalist2: [],
				chartLine: null,

			}
		},

		methods: {
drawLineChart2() {
	this.chartColumn2 = echarts.init(document.getElementById('chartColumn2'));
	this.chartColumn2.setOption({
		title: {
			text: '教师活跃统计图'
		},
		tooltip: {},
		xAxis: {
			data: this.timelist2,
		},
		yAxis: {},
		series: [{
			name: '销量',
			type: 'bar',
			data: this.datalist2,
		}],
	});
	
	},
			drawLineChart() {
				this.chartColumn = echarts.init(document.getElementById('chartColumn'));
				
				this.chartColumn.setOption({
					title: {
						text: '学生活跃统计图'
					},
					tooltip: {},
					xAxis: {
						data: this.timelist,
					},
					yAxis: {},
					series: [{
						name: '销量',
						type: 'bar',
						data: this.datalist,
					}],
				});
				
			},

			drawCharts() {
				this.selectStudentEchart()//查询学生活动
			this.selectTeacherEchart()//查询教师活动
			},
			selectStudentEchart() {
				selectStudent_echart().then(data => {


					for (var i = 0; i < data.list.length; i++) {
						this.timelist.push(String(data.list[i].time))
					}
					for (var i = 0; i < data.list.length; i++) {
						this.datalist.push(data.list[i].login_number)
					}
					var k = data.list.length - 7

					this.timelist = this.timelist.slice(k)
					this.datalist = this.datalist.slice(k)
					// for(var i=data.list.length-1;i>=3;i--){
					//  this.timelist.push(data.list[i])
					// }
					console.log(this.timelist)
					console.log(this.datalist)
					this.drawLineChart()
				});
			},
			selectTeacherEchart() {
				selectTeacher_echart().then(data => {
			
			
					for (var i = 0; i < data.list.length; i++) {
						this.timelist2.push(String(data.list[i].time))
					}
					for (var i = 0; i < data.list.length; i++) {
						this.datalist2.push(data.list[i].login_number)
					}
					var k = data.list.length - 7
			
					this.timelist2 = this.timelist2.slice(k)
					this.datalist2 = this.datalist2.slice(k)
					// for(var i=data.list.length-1;i>=3;i--){
					//  this.timelist.push(data.list[i])
					// }
					console.log(this.timelist2)
					console.log(this.datalist2)
					this.drawLineChart2()
				});
			}
		},

		mounted: function() {

			this.drawCharts()

		},
		updated: function() {

			this.drawCharts()
		}
	}
</script>

<style scoped>
	.chart-container {
		width: 100%;
		float: left;
	}

	/*.chart div {
        height: 400px;
        float: left;
    }*/

	.el-col {
		padding: 30px 20px;
	}
</style>



