import axios from 'axios';

let base = '';
//admin管理员有关所以接口
export const requestLogin = params => {//登录接口
	return axios.post(`http://biyeji.uicp.net:3000/admin/Login`, params).then(res => res.data);
};
export const getadminList = params => {//获取所有管理员账号信息
	return axios.post(`http://biyeji.uicp.net:3000/admin/getList2`, params).then(res => res.data);
};
export const removeUser = params => {//删除管理员账号
return axios.post(`http://biyeji.uicp.net:3000/admin/delete`, params).then(res => res.data);
};
export const addUser = params => {//新增管理员账号
return axios.post(`http://biyeji.uicp.net:3000/admin/regedit`, params).then(res => res.data);
};
export const editUser = params => {//更新管理员账号
return axios.post(`http://biyeji.uicp.net:3000/admin/update`, params).then(res => res.data);
};

